"use client";

import React from "react";

interface TextEditorToolbarProps {
  bold: boolean;
  italic: boolean;
  underline: boolean;
  fontSize: number;
  fontColor: string;
  onToggleBold: () => void;
  onToggleItalic: () => void;
  onToggleUnderline: () => void;
  onFontSizeChange: (size: number) => void;
  onFontColorChange: (color: string) => void;
}

export default function TextEditorToolbar({
  bold,
  italic,
  underline,
  fontSize,
  fontColor,
  onToggleBold,
  onToggleItalic,
  onToggleUnderline,
  onFontSizeChange,
  onFontColorChange,
}: TextEditorToolbarProps) {
  return (
    <div className="flex items-center space-x-3 p-2 border-t border-gray-700 bg-gray-900">
      <button
        onClick={onToggleBold}
        className={`px-2 py-1 rounded ${bold ? "bg-blue-600 text-white" : "text-gray-300"}`}
        title="Bold"
      >
        <b>B</b>
      </button>
      <button
        onClick={onToggleItalic}
        className={`px-2 py-1 rounded ${italic ? "bg-blue-600 text-white" : "text-gray-300"}`}
        title="Italic"
      >
        <i>I</i>
      </button>
      <button
        onClick={onToggleUnderline}
        className={`px-2 py-1 rounded ${underline ? "bg-blue-600 text-white" : "text-gray-300"}`}
        title="Underline"
      >
        <u>U</u>
      </button>
      <select
        value={fontSize}
        onChange={(e) => onFontSizeChange(parseInt(e.target.value))}
        className="bg-gray-800 text-white rounded px-2 py-1"
        title="Font Size"
      >
        {[12, 14, 16, 18, 20, 24, 28, 32].map((size) => (
          <option key={size} value={size}>
            {size}px
          </option>
        ))}
      </select>
      <input
        type="color"
        value={fontColor}
        onChange={(e) => onFontColorChange(e.target.value)}
        title="Font Color"
        className="w-8 h-8 p-0 border-0 rounded cursor-pointer"
      />
    </div>
  );
}
