"use client";

import React, { useState, useRef, useEffect } from "react";
import NoteSettings from "./NoteSettings";

interface Note {
  id: string;
  title: string;
}

interface Folder {
  id: string;
  name: string;
  color: string;
  passwordProtected: boolean;
  password?: string;
  locked: boolean;
  position: { x: number; y: number };
  size?: { width: number; height: number };
  notes: string[];
}

interface FolderProps {
  folder: Folder;
  notes: Note[];
  onUpdate: (id: string, updatedFields: Partial<Folder>) => void;
  onDelete: (id: string) => void;
  onRequestUnlock: () => void;
  onRequestSetPassword: () => void;
  onRequestChangePassword?: () => void;
  onDropNote: (noteId: string, folderId: string) => void;
  onNoteClick?: (noteId: string) => void;
}

export default function Folder({
  folder,
  notes,
  onUpdate,
  onDelete,
  onRequestUnlock,
  onRequestSetPassword,
  onRequestChangePassword,
  onDropNote,
  onNoteClick,
}: FolderProps) {
  const [name, setName] = useState(folder.name);
  const [color, setColor] = useState(folder.color);
  const [passwordProtected, setPasswordProtected] = useState(folder.passwordProtected);
  const [dragging, setDragging] = useState(false);
  const [resizing, setResizing] = useState(false);
  const [position, setPosition] = useState(folder.position);
  const [size, setSize] = useState(folder.size || { width: 300, height: 400 });
  const [showSettings, setShowSettings] = useState(false);
  const [showNotesList, setShowNotesList] = useState(false);

  const folderRef = useRef<HTMLDivElement>(null);
  const dragOffset = useRef<{ x: number; y: number }>({ x: 0, y: 0 });
  const resizeStart = useRef<{ x: number; y: number; width: number; height: number }>({ x: 0, y: 0, width: 0, height: 0 });

  useEffect(() => {
    setName(folder.name);
    setColor(folder.color);
    setPasswordProtected(folder.passwordProtected);
    setPosition(folder.position);
    if (folder.size) setSize(folder.size);
  }, [folder]);

  const getFolderTitle = () => {
    return name || 'Untitled Folder';
  };

  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newName = e.target.value.slice(0, 30);
    setName(newName);
    onUpdate(folder.id, { name: newName });
  };

  const handleColorChange = (newColor: string) => {
    setColor(newColor);
    onUpdate(folder.id, { color: newColor });
  };

  const handlePasswordToggle = () => {
    if (passwordProtected) {
      setPasswordProtected(false);
      onUpdate(folder.id, { passwordProtected: false, password: "", locked: false });
    } else {
      onRequestSetPassword();
    }
  };

  const handleMouseDown = (e: React.MouseEvent) => {
    if (folder.locked) return;
    setDragging(true);
    const rect = folderRef.current?.getBoundingClientRect();
    if (rect) {
      dragOffset.current = {
        x: e.clientX - rect.left,
        y: e.clientY - rect.top,
      };
    }
  };

  const handleResizeStart = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (folder.locked) return;
    setResizing(true);
    const rect = folderRef.current?.getBoundingClientRect();
    if (rect) {
      resizeStart.current = {
        x: e.clientX,
        y: e.clientY,
        width: rect.width,
        height: rect.height
      };
    }
  };

  const handleMouseMove = (e: MouseEvent) => {
    if (dragging) {
      const newX = e.clientX - dragOffset.current.x;
      const newY = e.clientY - dragOffset.current.y;
      setPosition({ x: newX, y: newY });
    } else if (resizing) {
      const deltaX = e.clientX - resizeStart.current.x;
      const deltaY = e.clientY - resizeStart.current.y;
      const newWidth = Math.max(300, resizeStart.current.width + deltaX);
      const newHeight = Math.max(400, resizeStart.current.height + deltaY);
      setSize({ width: newWidth, height: newHeight });
    }
  };

  const handleMouseUp = () => {
    if (dragging) {
      setDragging(false);
      onUpdate(folder.id, { position });
    } else if (resizing) {
      setResizing(false);
      onUpdate(folder.id, { size });
    }
  };

  useEffect(() => {
    if (dragging || resizing) {
      window.addEventListener("mousemove", handleMouseMove);
      window.addEventListener("mouseup", handleMouseUp);
    } else {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    }
    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    };
  }, [dragging, resizing, position, size]);

  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    const noteId = e.dataTransfer.getData("text/plain");
    if (noteId) {
      onDropNote(noteId, folder.id);
    }
  };

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
  };

  // Close settings menu and auto-lock on outside click
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (folderRef.current && !folderRef.current.contains(event.target as Node)) {
        setShowSettings(false);
        // Auto-lock if password protected
        if (passwordProtected && !folder.locked) {
          onUpdate(folder.id, { locked: true });
        }
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [folderRef, passwordProtected, folder.id, folder.locked]);

  // Get note titles for the notes in this folder
  const folderNotes = folder.notes.map(noteId => {
    const note = notes.find(n => n.id === noteId);
    return {
      id: noteId,
      title: note?.title || "Untitled Note"
    };
  });

  return (
    <div
      ref={folderRef}
      className="absolute rounded-lg shadow-lg p-4 cursor-default select-none transition-shadow duration-300 hover:shadow-2xl bg-gray-800"
      style={{
        width: `${size.width}px`,
        height: `${size.height}px`,
        borderTop: `0.5cm solid ${color}`,
        top: position.y,
        left: position.x,
        zIndex: dragging || resizing ? 1000 : 1,
      }}
      onDrop={handleDrop}
      onDragOver={handleDragOver}
    >
      <div
        className="h-[0.5cm] w-full cursor-move absolute top-0 left-0"
        onMouseDown={handleMouseDown}
      />
      {folder.locked ? (
        <div className="p-6 font-semibold flex flex-col items-center justify-center h-full text-white">
          <p className="mb-4 text-lg font-semibold text-center">{getFolderTitle()}</p>
          <p className="mb-4 text-sm text-gray-400">{folder.notes.length} notes</p>
          <button
            onClick={(e) => {
              e.stopPropagation();
              onRequestUnlock();
            }}
            className="bg-gray-700 text-white px-4 py-2 rounded hover:bg-gray-600 transition"
          >
            Unlock
          </button>
        </div>
      ) : (
        <>
          <input
            type="text"
            value={name}
            onChange={handleNameChange}
            placeholder="Folder Name"
            maxLength={30}
            className="w-full p-2 mb-4 rounded bg-gray-900 text-white border border-gray-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
          
          {/* Notes List Widget */}
          <div className="flex-1 overflow-auto mb-16 bg-gray-900 rounded p-2 max-h-[calc(100%-8rem)]">
            <h3
              className="text-sm font-semibold mb-2 text-gray-400 cursor-pointer select-none"
              onClick={() => setShowNotesList(!showNotesList)}
            >
              Notes in folder: {showNotesList ? "▲" : "▼"}
            </h3>
            {showNotesList && (
              folderNotes.length === 0 ? (
                <p className="text-sm text-gray-500 italic">No notes in this folder</p>
              ) : (
                <ul className="space-y-1">
                  {folderNotes.map((note) => (
                    <li 
                      key={note.id} 
                      className="text-sm text-white bg-gray-800 rounded px-2 py-1 flex items-center hover:bg-gray-700 cursor-pointer transition-colors"
                      onClick={() => onNoteClick?.(note.id)}
                    >
                      <span className="truncate flex-1">{note.title}</span>
                    </li>
                  ))}
                </ul>
              )
            )}
          </div>

          <div className="absolute bottom-0 left-0 right-0 h-16 flex items-center justify-center bg-gray-900 rounded-b-lg">
            <button
              onClick={() => setShowSettings(!showSettings)}
              className="p-2 rounded-full hover:bg-gray-700 transition"
              draggable={!folder.locked}
              onDragStart={(e) => {
                e.stopPropagation();
                e.dataTransfer.setData("text/plain", folder.id);
              }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="20"
                height="20"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                className="text-gray-300"
              >
                <rect x="2" y="4" width="20" height="16" rx="2"/>
                <path d="m22 4-10 8L2 4"/>
              </svg>
            </button>
            <NoteSettings
              color={color}
              onColorChange={handleColorChange}
              passwordProtected={passwordProtected}
              onTogglePassword={handlePasswordToggle}
              onDelete={() => onDelete(folder.id)}
              isOpen={showSettings}
              onClose={() => setShowSettings(false)}
              isUnlocked={!folder.locked}
              onChangePassword={onRequestChangePassword}
            />
          </div>

          {/* Resize handle with visual indicator */}
          <div
            className="absolute bottom-0 right-0 w-6 h-6 cursor-se-resize group flex items-center justify-center"
            onMouseDown={handleResizeStart}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              fill="currentColor"
              className="w-4 h-4 text-gray-400 group-hover:text-white transition-colors"
            >
              <path d="M21 21l-6-6m6 6l-6 0m6 0l0-6" strokeWidth="2" stroke="currentColor"/>
            </svg>
          </div>
        </>
      )}
    </div>
  );
}
