"use client";

import React, { useState, useRef, useEffect } from "react";
import NoteSettings from "./NoteSettings";
import TextEditorToolbar from "./TextEditorToolbar";

interface Note {
  id: string;
  title: string;
  content: string;
  color: string;
  passwordProtected: boolean;
  password?: string;
  locked: boolean;
  position: { x: number; y: number };
  size?: { width: number; height: number };
}

interface StickyNoteProps {
  note: Note;
  onUpdate: (id: string, updatedFields: Partial<Note>) => void;
  onDelete: (id: string) => void;
  onRequestUnlock: () => void;
  onRequestSetPassword: () => void;
  onRequestChangePassword?: () => void;
}

export default function StickyNote({
  note,
  onUpdate,
  onDelete,
  onRequestUnlock,
  onRequestSetPassword,
  onRequestChangePassword,
}: StickyNoteProps) {
  const [content, setContent] = useState(note.content);
  const [title, setTitle] = useState(note.title || "");
  const [color, setColor] = useState(note.color);
  const [passwordProtected, setPasswordProtected] = useState(note.passwordProtected);
  const [password, setPassword] = useState(note.password || "");
  const [dragging, setDragging] = useState(false);
  const [resizing, setResizing] = useState(false);
  const [position, setPosition] = useState(note.position);
  const [size, setSize] = useState(note.size || { width: 300, height: 400 });
  const [showSettings, setShowSettings] = useState(false);
  const [hasSelection, setHasSelection] = useState(false);

  // Text formatting states
  const [bold, setBold] = useState(false);
  const [italic, setItalic] = useState(false);
  const [underline, setUnderline] = useState(false);
  const [fontSize, setFontSize] = useState(16);
  const [fontColor, setFontColor] = useState("#ffffff");

  const noteRef = useRef<HTMLDivElement>(null);
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const dragOffset = useRef<{ x: number; y: number }>({ x: 0, y: 0 });
  const resizeStart = useRef<{ x: number; y: number, width: number, height: number }>({ x: 0, y: 0, width: 0, height: 0 });

  useEffect(() => {
    setContent(note.content);
    setTitle(note.title || "");
    setColor(note.color);
    setPasswordProtected(note.passwordProtected);
    setPassword(note.password || "");
    setPosition(note.position);
    if (note.size) setSize(note.size);
  }, [note]);

  const handleTitleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newTitle = e.target.value.slice(0, 30);
    setTitle(newTitle);
    onUpdate(note.id, { title: newTitle });
  };

  const handleContentChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setContent(e.target.value);
    onUpdate(note.id, { content: e.target.value });
  };

  const handleSelectionChange = () => {
    if (textareaRef.current) {
      const hasTextSelection = 
        textareaRef.current.selectionStart !== textareaRef.current.selectionEnd;
      setHasSelection(hasTextSelection);
    }
  };

  const handleColorChange = (newColor: string) => {
    setColor(newColor);
    onUpdate(note.id, { color: newColor });
  };

  const handlePasswordToggle = () => {
    if (passwordProtected) {
      setPasswordProtected(false);
      setPassword("");
      onUpdate(note.id, { passwordProtected: false, password: "", locked: false });
    } else {
      onRequestSetPassword();
    }
  };

  const handleMouseDown = (e: React.MouseEvent) => {
    if (note.locked) return;
    setDragging(true);
    const rect = noteRef.current?.getBoundingClientRect();
    if (rect) {
      dragOffset.current = {
        x: e.clientX - rect.left,
        y: e.clientY - rect.top,
      };
    }
  };

  const handleResizeStart = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (note.locked) return;
    setResizing(true);
    const rect = noteRef.current?.getBoundingClientRect();
    if (rect) {
      resizeStart.current = {
        x: e.clientX,
        y: e.clientY,
        width: rect.width,
        height: rect.height
      };
    }
  };

  const handleMouseMove = (e: MouseEvent) => {
    if (dragging) {
      const newX = e.clientX - dragOffset.current.x;
      const newY = e.clientY - dragOffset.current.y;
      setPosition({ x: newX, y: newY });
    } else if (resizing) {
      const deltaX = e.clientX - resizeStart.current.x;
      const deltaY = e.clientY - resizeStart.current.y;
      // Remove minimum size restrictions for resizing
      const newWidth = Math.max(100, resizeStart.current.width + deltaX); // Smaller minimum width
      const newHeight = Math.max(100, resizeStart.current.height + deltaY); // Smaller minimum height
      setSize({ width: newWidth, height: newHeight });
    }
  };

  const handleMouseUp = () => {
    if (dragging) {
      setDragging(false);
      onUpdate(note.id, { position });
    } else if (resizing) {
      setResizing(false);
      onUpdate(note.id, { size });
    }
  };

  useEffect(() => {
    if (dragging || resizing) {
      window.addEventListener("mousemove", handleMouseMove);
      window.addEventListener("mouseup", handleMouseUp);
    } else {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    };
    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
      window.removeEventListener("mouseup", handleMouseUp);
    };
  }, [dragging, resizing, position, size]);

  // Close settings menu and auto-lock on outside click
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (noteRef.current && !noteRef.current.contains(event.target as Node)) {
        setShowSettings(false);
        // Auto-lock if password protected
        if (passwordProtected && !note.locked) {
          onUpdate(note.id, { locked: true });
        }
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [noteRef, passwordProtected, note.id, note.locked]);

  return (
    <div
      ref={noteRef}
      className="absolute rounded-lg shadow-lg cursor-default select-none transition-shadow duration-300 hover:shadow-2xl"
      style={{
        width: `${size.width}px`,
        height: `${size.height}px`,
        backgroundColor: "rgba(0, 0, 0, 0.8)",
        color: "white",
        borderTop: `0.5cm solid ${color}`,
        top: position.y,
        left: position.x,
        zIndex: dragging || resizing ? 1000 : 1,
      }}
    >
      <div
        className="h-[0.5cm] w-full cursor-move absolute top-0 left-0"
        onMouseDown={handleMouseDown}
      />
      {note.locked ? (
        <div className="p-6 font-semibold flex flex-col items-center justify-center h-full">
          <p className="mb-4 text-lg font-semibold text-center">{title || "Untitled Note"}</p>
          <button
            onClick={(e) => {
              e.stopPropagation();
              onRequestUnlock();
            }}
            className="bg-gray-800 text-white px-4 py-2 rounded hover:bg-gray-700 transition"
          >
            Unlock
          </button>
        </div>
      ) : (
        <>
          <input
            type="text"
            value={title}
            onChange={handleTitleChange}
            placeholder="Note Title"
            maxLength={30}
            className="w-full p-2 mb-2 rounded-t bg-gray-900 text-white border border-gray-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
          <textarea
            ref={textareaRef}
            className="w-full h-[calc(100%-8rem)] p-3 resize-none border border-gray-300 bg-transparent focus:outline-none focus:ring-2 focus:ring-blue-500 transition"
            value={content}
            onChange={handleContentChange}
            onSelect={handleSelectionChange}
            onMouseUp={handleSelectionChange}
            onKeyUp={handleSelectionChange}
            placeholder="Write your note here..."
            style={{
              color: fontColor,
              fontWeight: bold ? "bold" : "normal",
              fontStyle: italic ? "italic" : "normal",
              textDecoration: underline ? "underline" : "none",
              fontSize: `${fontSize}px`,
            }}
          />
          
          <div className="absolute bottom-0 left-0 right-0 h-16 flex items-center justify-center bg-gray-900 rounded-b-lg">
            <div className="flex flex-col items-center">
              <button
                onClick={() => setShowSettings(!showSettings)}
                className="p-2 rounded-full hover:bg-gray-700 transition"
                draggable={!note.locked}
                onDragStart={(e) => {
                  e.stopPropagation();
                  e.dataTransfer.setData("text/plain", note.id);
                }}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  className="text-gray-300"
                >
                  <rect x="2" y="4" width="20" height="16" rx="2"/>
                  <path d="m22 4-10 8L2 4"/>
                </svg>
              </button>
              {hasSelection && (
                <TextEditorToolbar
                  bold={bold}
                  italic={italic}
                  underline={underline}
                  fontSize={fontSize}
                  fontColor={fontColor}
                  onToggleBold={() => setBold(!bold)}
                  onToggleItalic={() => setItalic(!italic)}
                  onToggleUnderline={() => setUnderline(!underline)}
                  onFontSizeChange={(size) => setFontSize(size)}
                  onFontColorChange={(color) => setFontColor(color)}
                />
              )}
            </div>
          </div>

          {showSettings && (
            <NoteSettings
              color={color}
              onColorChange={handleColorChange}
              passwordProtected={passwordProtected}
              onTogglePassword={handlePasswordToggle}
              onDelete={() => onDelete(note.id)}
              isOpen={showSettings}
              onClose={() => setShowSettings(false)}
              isUnlocked={!note.locked}
              onChangePassword={onRequestChangePassword}
            />
          )}

          {/* Resize handle with visual indicator */}
          <div
            className="absolute bottom-0 right-0 w-6 h-6 cursor-se-resize group flex items-center justify-center"
            onMouseDown={handleResizeStart}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              fill="currentColor"
              className="w-4 h-4 text-gray-400 group-hover:text-white transition-colors"
            >
              <path d="M21 21l-6-6m6 6l-6 0m6 0l0-6" strokeWidth="2" stroke="currentColor"/>
            </svg>
          </div>
        </>
      )}
    </div>
  );
}
