"use client";

import React, { useState } from "react";

interface Note {
  id: string;
  content: string;
  color: string;
  passwordProtected: boolean;
  password?: string;
  locked: boolean;
  position: { x: number; y: number };
}

interface Folder {
  id: string;
  name: string;
  color: string;
  passwordProtected: boolean;
  password?: string;
  locked: boolean;
  position: { x: number; y: number };
  notes: string[];
}

interface PasswordModalProps {
  note?: Note | null;
  folder?: Folder | null;
  mode: "unlock" | "set" | "change";
  onClose: () => void;
  onUnlock: (id: string, password: string) => void;
  onSetPassword: (id: string, password: string, oldPassword?: string) => void;
}

export default function PasswordModal({
  note,
  folder,
  mode,
  onClose,
  onUnlock,
  onSetPassword,
}: PasswordModalProps) {
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const item = note || folder;
  const itemType = note ? "Note" : "Folder";

  const handleConfirm = () => {
    if (!item) return;
    
    if (mode === "unlock") {
      onUnlock(item.id, oldPassword);
      setOldPassword("");
    } else if (mode === "set") {
      onSetPassword(item.id, newPassword);
      setNewPassword("");
    } else if (mode === "change") {
      onSetPassword(item.id, newPassword, oldPassword);
      setOldPassword("");
      setNewPassword("");
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-[9999]">
      <div className="bg-white rounded p-6 w-80 max-w-full text-black">
        <h2 className="text-lg font-semibold mb-4">
          {mode === "unlock" 
            ? `Unlock ${itemType}` 
            : mode === "set" 
              ? `Set ${itemType} Password` 
              : `Change ${itemType} Password`}
        </h2>
        
        {/* Old Password Field (for unlock and change modes) */}
        {(mode === "unlock" || mode === "change") && (
          <input
            type="password"
            value={oldPassword}
            onChange={(e) => setOldPassword(e.target.value)}
            placeholder={mode === "unlock" ? "Enter password" : "Enter current password"}
            className="w-full p-2 border border-gray-300 rounded mb-4 bg-white text-black"
          />
        )}

        {/* New Password Field (for set and change modes) */}
        {(mode === "set" || mode === "change") && (
          <input
            type="password"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            placeholder="Enter new password"
            className="w-full p-2 border border-gray-300 rounded mb-4 bg-white text-black"
          />
        )}

        <div className="flex justify-end space-x-2">
          <button
            onClick={onClose}
            className="px-4 py-2 bg-gray-300 text-black rounded hover:bg-gray-400"
          >
            Cancel
          </button>
          <button
            onClick={handleConfirm}
            className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
          >
            {mode === "unlock" 
              ? "Unlock" 
              : mode === "set" 
                ? "Set Password" 
                : "Change Password"}
          </button>
        </div>
      </div>
    </div>
  );
}
