"use client";

import React, { useState, useEffect } from "react";
import StickyNote from "../components/StickyNote";
import ColorPicker from "../components/ColorPicker";
import PasswordModal from "../components/PasswordModal";
import Folder from "../components/Folder";

interface Note {
  id: string;
  title: string;
  content: string;
  color: string;
  passwordProtected: boolean;
  password?: string;
  locked: boolean;
  position: { x: number; y: number };
  size?: { width: number; height: number };
  folderId?: string;
}

interface Folder {
  id: string;
  name: string;
  color: string;
  passwordProtected: boolean;
  password?: string;
  locked: boolean;
  position: { x: number; y: number };
  size?: { width: number; height: number };
  notes: string[];
}

export default function Page() {
  const [notes, setNotes] = useState<Note[]>([]);
  const [folders, setFolders] = useState<Folder[]>([]);
  const [selectedNote, setSelectedNote] = useState<Note | null>(null);
  const [selectedFolder, setSelectedFolder] = useState<Folder | null>(null);
  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [passwordModalMode, setPasswordModalMode] = useState<"unlock" | "set" | "change">("unlock");

  // Load notes and folders from server file on mount
  useEffect(() => {
    async function fetchData() {
      try {
        const res = await fetch('/api/notes');
        if (res.ok) {
          const data = await res.json();
          setNotes(data.notes || []);
          setFolders(data.folders || []);
        }
      } catch (error) {
        console.error('Failed to fetch data:', error);
      }
    }
    fetchData();
  }, []);

  // Save notes and folders to server file on change
  useEffect(() => {
    async function saveData() {
      try {
        await fetch('/api/notes', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ notes, folders }),
        });
      } catch (error) {
        console.error('Failed to save data:', error);
      }
    }
    saveData();
  }, [notes, folders]);

  const addNote = () => {
    const newNote: Note = {
      id: Date.now().toString(),
      title: "",
      content: "",
      color: "#fef3c7", // default sticky note yellow
      passwordProtected: false,
      locked: false,
      position: { x: 100, y: 100 },
      size: { width: 300, height: 400 }, // Set default size
    };
    setNotes((prev) => [...prev, newNote]);
  };

  const addFolder = () => {
    const newFolder: Folder = {
      id: Date.now().toString(),
      name: "New Folder",
      color: "#4b5563", // default gray color
      passwordProtected: false,
      locked: false,
      position: { x: 100, y: 100 },
      size: { width: 300, height: 400 }, // Match note size
      notes: [],
    };
    setFolders((prev) => [...prev, newFolder]);
  };

  const updateNote = (id: string, updatedFields: Partial<Note>) => {
    setNotes((prev) =>
      prev.map((note) => (note.id === id ? { ...note, ...updatedFields } : note))
    );
  };

  const updateFolder = (id: string, updatedFields: Partial<Folder>) => {
    setFolders((prev) =>
      prev.map((folder) => (folder.id === id ? { ...folder, ...updatedFields } : folder))
    );
  };

  const deleteNote = (id: string) => {
    // Remove note from any folder that contains it
    setFolders((prev) =>
      prev.map((folder) => ({
        ...folder,
        notes: folder.notes.filter((noteId) => noteId !== id),
      }))
    );
    // Remove note from notes array
    setNotes((prev) => prev.filter((note) => note.id !== id));
  };

  const deleteFolder = (id: string) => {
    // Move all notes from folder back to main workspace
    const folder = folders.find((f) => f.id === id);
    if (folder) {
      setNotes((prev) =>
        prev.map((note) =>
          folder.notes.includes(note.id) ? { ...note, folderId: undefined } : note
        )
      );
    }
    setFolders((prev) => prev.filter((folder) => folder.id !== id));
  };

  const moveNoteToFolder = (noteId: string, folderId: string) => {
    // Remove note from any other folder
    setFolders((prev) =>
      prev.map((folder) => ({
        ...folder,
        notes: folder.notes.filter((id) => id !== noteId),
      }))
    );
    // Add note to new folder
    setFolders((prev) =>
      prev.map((folder) =>
        folder.id === folderId
          ? { ...folder, notes: [...folder.notes, noteId] }
          : folder
      )
    );
    // Update note's folderId
    updateNote(noteId, { folderId });
  };

  const handleNoteClick = (noteId: string) => {
    const note = notes.find(n => n.id === noteId);
    if (note) {
      // If the note is in a folder, bring it out to the main workspace
      if (note.folderId) {
        // Remove note from folder
        setFolders(prev =>
          prev.map(folder => ({
            ...folder,
            notes: folder.notes.filter(id => id !== noteId)
          }))
        );
        // Update note to remove folderId and set new position
        updateNote(noteId, {
          folderId: undefined,
          position: { x: Math.random() * 100 + 100, y: Math.random() * 100 + 100 }
        });
      }
    }
  };

  const unlockNote = (id: string, password: string) => {
    const note = notes.find((n) => n.id === id);
    if (note && note.password === password) {
      updateNote(id, { locked: false });
      setShowPasswordModal(false);
      setSelectedNote(null);
    } else {
      alert("Incorrect password");
    }
  };

  const unlockFolder = (id: string, password: string) => {
    const folder = folders.find((f) => f.id === id);
    if (folder && folder.password === password) {
      updateFolder(id, { locked: false });
      setShowPasswordModal(false);
      setSelectedFolder(null);
    } else {
      alert("Incorrect password");
    }
  };

  const setNotePassword = (id: string, password: string, oldPassword?: string) => {
    const note = notes.find((n) => n.id === id);
    if (passwordModalMode === "change" && oldPassword !== note?.password) {
      alert("Incorrect old password");
      return;
    }
    updateNote(id, { password, locked: true, passwordProtected: true });
    setShowPasswordModal(false);
    setSelectedNote(null);
  };

  const setFolderPassword = (id: string, password: string, oldPassword?: string) => {
    const folder = folders.find((f) => f.id === id);
    if (passwordModalMode === "change" && oldPassword !== folder?.password) {
      alert("Incorrect old password");
      return;
    }
    updateFolder(id, { password, locked: true, passwordProtected: true });
    setShowPasswordModal(false);
    setSelectedFolder(null);
  };

  return (
    <div className="min-h-screen bg-black text-white p-4 font-sans" style={{ fontFamily: "'Inter', sans-serif" }}>
      <header className="flex justify-between items-center mb-6">
        <h1 className="text-4xl font-extrabold tracking-tight">Sticky Notes</h1>
        <div className="flex gap-4">
          <button
            onClick={addNote}
            className="bg-white text-black px-5 py-2 rounded-lg shadow-md hover:bg-gray-200 transition"
          >
            Add Note
          </button>
          <button
            onClick={addFolder}
            className="bg-gray-700 text-white px-5 py-2 rounded-lg shadow-md hover:bg-gray-600 transition border border-gray-600"
          >
            Add Folder
          </button>
          <a
            href="/api/download"
            className="bg-blue-600 text-white px-5 py-2 rounded-lg shadow-md hover:bg-blue-700 transition flex items-center gap-2"
            download
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
              <polyline points="7 10 12 15 17 10" />
              <line x1="12" y1="15" x2="12" y2="3" />
            </svg>
            Download Project
          </a>
        </div>
      </header>
      <main className="relative h-[80vh] border border-gray-700 rounded-lg overflow-auto bg-gray-900 p-6 shadow-inner">
        {/* Render folders */}
        {folders.map((folder) => (
          <Folder
            key={folder.id}
            folder={folder}
            notes={notes}
            onUpdate={updateFolder}
            onDelete={deleteFolder}
            onRequestUnlock={() => {
              setSelectedFolder(folder);
              setShowPasswordModal(true);
              setPasswordModalMode("unlock");
            }}
            onRequestSetPassword={() => {
              setSelectedFolder(folder);
              setShowPasswordModal(true);
              setPasswordModalMode("set");
            }}
            onRequestChangePassword={() => {
              setSelectedFolder(folder);
              setShowPasswordModal(true);
              setPasswordModalMode("change");
            }}
            onDropNote={moveNoteToFolder}
            onNoteClick={handleNoteClick}
          />
        ))}
        
        {/* Render notes that are not in folders */}
        {notes
          .filter((note) => !note.folderId)
          .map((note) => (
            <StickyNote
              key={note.id}
              note={note}
              onUpdate={updateNote}
              onDelete={deleteNote}
              onRequestUnlock={() => {
                setSelectedNote(note);
                setShowPasswordModal(true);
                setPasswordModalMode("unlock");
              }}
              onRequestSetPassword={() => {
                setSelectedNote(note);
                setShowPasswordModal(true);
                setPasswordModalMode("set");
              }}
              onRequestChangePassword={() => {
                setSelectedNote(note);
                setShowPasswordModal(true);
                setPasswordModalMode("change");
              }}
            />
          ))}
      </main>
      {(selectedNote || selectedFolder) && showPasswordModal && (
        <PasswordModal
          note={selectedNote}
          folder={selectedFolder}
          mode={passwordModalMode}
          onClose={() => {
            setShowPasswordModal(false);
            setPasswordModalMode("unlock");
            setSelectedNote(null);
            setSelectedFolder(null);
          }}
          onUnlock={selectedNote ? unlockNote : unlockFolder}
          onSetPassword={selectedNote ? setNotePassword : setFolderPassword}
        />
      )}
    </div>
  );
}
