"use client";

import React, { useState } from "react";

interface NoteSettingsProps {
  color: string;
  onColorChange: (color: string) => void;
  passwordProtected: boolean;
  onTogglePassword: () => void;
  onDelete: () => void;
  isOpen: boolean;
  onClose: () => void;
  isUnlocked?: boolean;
  onChangePassword?: () => void;
}

export default function NoteSettings({
  color,
  onColorChange,
  passwordProtected,
  onTogglePassword,
  onDelete,
  isOpen,
  onClose,
  isUnlocked,
  onChangePassword,
}: NoteSettingsProps) {
  const [showPasswordSettings, setShowPasswordSettings] = useState(false);

  if (!isOpen) return null;

  return (
    <div className="absolute bottom-16 left-0 right-0 bg-gray-900 rounded-lg p-4 text-white border border-gray-700">
      <div className="flex flex-col gap-3">
        <div className="flex items-center justify-between">
          <span className="text-sm">Border Color</span>
          <input
            type="color"
            value={color}
            onChange={(e) => onColorChange(e.target.value)}
            className="cursor-pointer w-8 h-8 p-0 border-0 rounded bg-transparent"
          />
        </div>
        
        <div className="flex items-center justify-between">
          <span className="text-sm">Security Settings</span>
          <button
            onClick={() => setShowPasswordSettings(!showPasswordSettings)}
            className="p-2 rounded-full hover:bg-gray-700 transition"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="text-gray-300"
            >
              {passwordProtected ? (
                // Locked padlock icon
                <path d="M12 1a3 3 0 0 0-3 3v8a3 3 0 0 0 6 0V4a3 3 0 0 0-3-3z M19 10h-2a7 7 0 0 0-14 0H1v10a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V10z" />
              ) : (
                // Unlocked padlock icon
                <path d="M12 1a3 3 0 0 0-3 3v8a3 3 0 0 0 6 0V4a3 3 0 0 0-3-3z M19 10H5a2 2 0 0 0-2 2v10a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2V12a2 2 0 0 0-2-2z" />
              )}
            </svg>
          </button>
        </div>

        {showPasswordSettings && (
          <div className="pl-4 flex flex-col gap-2">
            <button
              onClick={onTogglePassword}
              className="px-3 py-1 bg-blue-600 text-white rounded hover:bg-blue-700 text-sm w-full text-left"
            >
              {passwordProtected ? "Disable Password" : "Enable Password"}
            </button>
            {passwordProtected && isUnlocked && onChangePassword && (
              <button
                onClick={onChangePassword}
                className="px-3 py-1 bg-blue-600 text-white rounded hover:bg-blue-700 text-sm w-full text-left"
              >
                Change Password
              </button>
            )}
          </div>
        )}

        <div className="flex items-center justify-between">
          <span className="text-sm">Delete</span>
          <button
            onClick={onDelete}
            className="px-3 py-1 bg-red-600 text-white rounded hover:bg-red-700 text-sm"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  );
}
